div {
  box-sizing: border-box;
}
.container {
  width: 100%;
  height: 100vh;
  padding: 12px;
}
.section {
  width: 100%;
  padding: 12px;
  border: 1px solid #123456;
  border-radius: 12px;
  height: 125px;
}
.section:not(:first-child) {
  margin-top: 12px;
}
.description {
  width: 100%;
  padding: 12px;
  color: #123456;
}
.content {
  position: relative;
  width: 100%;
  padding: 12px;
}
.box {
  position: absolute;
  width: 100px;
  height: 25px;
  border: 1px solid black;
  border-radius: 12px;
  margin: 0;
}

/* grid layout */
.grid {
  display: grid;
  margin-top: 12px;
  grid-template-columns: repeat(4, 1fr);
  gap: 12px;
}
.grid__section {
  display: grid;
  grid-auto-flow: column;
  grid-template-rows: 15% 85%;
  height: 100%;
  border: 1px solid #123456;
  border-radius: 12px;
  margin-top: 0px;
  height: 375px;
}
.grid__box {
  position: absolute;
  width: 25px;
  height: 100px;
  border: 1px solid black;
  border-radius: 12px;
  margin: 0;
}
.grid__description {
  width: 100%;
  padding: 12px;
  color: #123456;
}
.grid__content {
  position: relative;
  width: 100%;
  padding: 12px;
}

/* animations */
.left-to-right__5s {
  animation-name: left-to-right__5s;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}
.left-to-right__5s_percent {
  animation-name: left-to-right__5s_percent;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}
.left-to-right__10s_100percent {
  animation-name: left-to-right__10s_100percent;
  animation-duration: 5s;
  animation-iteration-count: infinite;
}
.right-to-left__5s {
  animation: right-to-left__5s 5s infinite;
}
.up-to-down__5s {
  animation: up-to-down__5s 5s infinite;
}
.up-to-down__5s_percent {
  animation: up-to-down__5s_percent 5s infinite;
}
.down-to-up__5s {
  animation: down-to-up__5s 5s infinite;
}
.down-to-up__5s_percent {
  animation: down-to-up__5s_percent 5s infinite;
}

/* @keyframes rule */
@keyframes left-to-right__5s {
  from { left: 0; }
  to { left: 300px; }
}
@keyframes left-to-right__5s_percent {
  0% { left: 0; }
  50% { left: 150px; }
  100% { left: 300px; }
}
@keyframes left-to-right__10s_100percent {
  from { left: 0; }
  to { left: calc(100% - 100px); }
}
@keyframes right-to-left__5s {
  from { right: 0; }
  to { right: 300px; }
}
@keyframes up-to-down__5s {
  from { top: 0; }
  to { top: calc(100% - 100px); }
}
@keyframes up-to-down__5s_percent {
  0% { top: 0; }
  10% { top: 10%; }
  20% { top: 20%; }
  30% { top: 30%; }
  40% { top: 40%; }
  50% { top: 50%; }
  60% { top: 60%; }
  70% { top: 70%; }
  80% { top: 80%; }
  90% { top: calc(90% - 90px); }
  100% { top: calc(100% - 100px); }
}
@keyframes down-to-up__5s {
  from { bottom: 0; }
  to { bottom: calc(100% - 100px); }
}
@keyframes down-to-up__5s_percent {
  0% { bottom: 0; }
  10% { bottom: 10%; }
  20% { bottom: 20%; }
  30% { bottom: 30%; }
  40% { bottom: 40%; }
  50% { bottom: 50%; }
  60% { bottom: 60%; }
  70% { bottom: 70%; }
  80% { bottom: 80%; }
  90% { bottom: 90%; }
  100% { bottom: 100%; }
}